

body {
    background-color: rgb(196, 140, 196);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    box-sizing: border-box;
    font-weight: 400;
    font-family: 'Permanent Marker', cursive;
    height: 90vh;
}

.container h1 {
    text-align: center;
}

#quote-box {
    position: relative;
    background-color: aliceblue;
    color: rgb(38, 104, 82);
    padding: 35px;
    font-size: 1.5em;
    min-width: 699px;
    max-width: 700px;
    min-height: 400px;
}

#text{
    text-align: center;
    padding: 20px 30px;
}

#author {
    color: rgb(61, 61, 61);
    text-align: right;
}

.text-author-container {
    height: 300px;
}

.twitter-button-container{    
    height: 150px;
}

.bottom-spaceout {
    display: flex;
    justify-content:space-between;
    align-items: flex-end;
}

#tweet-quote {
    color: rgb(255, 255, 255);
    border-radius: 5px;
    background-color: rgb(59, 134, 134);
    height: 40px;
    width: 40px;
    display: flex;
    justify-content: center;
    align-items:center;
}

#new-quote {
    color: rgb(255, 255, 255);
    border-radius: 5px;
    background-color: rgb(59, 134, 134);
    border: transparent;
    height: 40px;
}
#new-quote:hover, #tweet-quote:hover{
    background-color: rgb(118, 184, 184);
}


.hover-pointer:hover {
    cursor: pointer;
}

.footer {
    display: flex;
    justify-content: center;
    color: rgb(214, 193, 214);
}